import React from 'react';
import { createRoot } from 'react-dom/client';
import store from './store';
import { Provider } from 'react-redux';
import './index.css';
import App from './App';
import Register from './components/Register/Register';
import Login from './components/Login/Login';
import User from './components/User/User';
import Home from './components/Home/Home';
import PostList from './components/PostList/PostList';
import PostForm from './components/PostForm/PostForm';
import PostNew from './components/PostNew/PostNew';
import {
  createBrowserRouter,
  RouterProvider
} from "react-router-dom";
// import { Provider } from 'react-redux';
// import store from './store';
// get rid of outlet and children path
// all paths are the same level

const router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
    children: [
      {
        path: "login",
        element: <Login />,
      },
      {
        path: "register",
        element: <Register />
      },
      {
        path: "/",
        element: <User />,
        children: [
          // index routes render into their parent's route/URL
          // index component: placeholder component
          // children componenturl/route, urloutletrenderchild component
          // indexurl/routeparent, parent url, outletrender index component
          // outletrender child component, parent urlone of child routes' path, child url, empty outlet, parent page
          // indexdefault child routefill parent's empty space 
          // parentApp, homeapp route, empty outlet
          {
            index: true,
            element: <Home />
          },
          {
            path: "post",
            element: <PostList />,
          },
          {
            path: "post/:id",
            element: <PostForm />,
          },
          {
            path: "post/new",
            element: <PostNew />,
          }
        ],
      }
    ]
  },
]);

const root = createRoot(document.getElementById('root'));
root.render(
  <Provider store={store}>
    <RouterProvider router={router} />
  </Provider>
);
